import {slideDown, slideUp} from "./js/utils";

require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
import Choices from "choices.js";
import 'choices.js/public/assets/styles/choices.min.css'
import {RunHeader} from "./js/header";
import debounce from "lodash/debounce";
import {ResponsiveSlider} from "./js/ResponsiveSlider";
import '@fortawesome/fontawesome-free/css/all.css';
import Inputmask from "inputmask/lib/inputmask.js";


Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);


document.addEventListener("DOMContentLoaded", (event) => {

        const header = document.querySelector('.header');
        new RunHeader(header);


        class ResponsiveSwiper {
            constructor(selector) {
                this.swipersEnablesFlag = false;
                this.swiperClass = selector;
                this.swipers = [];
                this.init();
                window.addEventListener('resize', this.handleResize.bind(this));
            }

            init() {
                // Проверяем ширину экрана
                if (window.innerWidth < 768) {
                    this.enableSwipers();
                } else {
                    this.destroySwipers();
                }
            }

            enableSwipers() {
                const swiperElements = document.querySelectorAll(this.swiperClass);

                swiperElements.forEach((element, index) => {
                    const nextEl = element.closest('.slider-cards-block').querySelector('.nav-next');
                    const prevEl = element.closest('.slider-cards-block').querySelector('.nav-prev');

                    const swiper = new Swiper(element, {
                        slidesPerView: 1,
                        navigation: {
                            nextEl: nextEl,
                            prevEl: prevEl,
                        },
                    });

                    this.swipers.push(swiper);
                });
                this.swipersEnablesFlag = true;
            }

            destroySwipers() {
                this.swipers.forEach(swiper => swiper.destroy(true, true));
                this.swipers = [];
                this.swipersEnablesFlag = false;
            }

            handleResize() {
                // Уничтожаем или создаем Swiper при изменении размера окна
                if (window.innerWidth < 768 && this.swipers.length === 0 && !this.swipersEnablesFlag) {
                    this.enableSwipers();
                } else if (window.innerWidth >= 768 && this.swipers.length > 0 && this.swipersEnablesFlag) {
                    this.destroySwipers();
                }
            }
        }

// Инициализация класса для слайдеров с классом .slider-cards-block__slider
        const responsiveSwiper = new ResponsiveSwiper('.slider-cards-block__slider');


        function scrollToElementWithHeaderOffset(element, header) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight; // Позиция с учетом высоты хедера

            // Прокручиваем страницу на нужное расстояние
            window.scrollBy({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = parseInt(btn.getAttribute('data-tab'));
                        for (let block of blocks) {
                            if (parseInt(block.getAttribute('data-tab-block')) === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(block);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (parseInt(otherBtn.getAttribute('data-tab')) !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }


// faq-page
        const faqs = document.querySelectorAll('.faq-block');

        if (faqs.length) {
            for (const accordeon of faqs) {
                const button = accordeon.querySelector('.faq-block__question');
                const answer = accordeon.querySelector('.faq-block__answer');

                button.addEventListener('click', (evt) => {
                    evt.stopImmediatePropagation();
                    if (accordeon.classList.contains('faq-block--active')) {
                        slideUp(answer, 300, () => {
                        });
                        accordeon.classList.remove('faq-block--active');
                    } else {
                        for (const otherAccordeon of faqs) {
                            if (otherAccordeon !== accordeon) {
                                otherAccordeon.classList.remove('faq-block--active');
                                const otherAnswer = otherAccordeon.querySelector('.faq-block__answer');
                                slideUp(otherAnswer, 300, () => {
                                });
                            }
                        }
                        slideDown(answer, 300, () => {
                        });
                        accordeon.classList.add('faq-block--active');
                    }
                });
            }
        }

        const formSelectEls = document.querySelectorAll('.form-select select');
        if (formSelectEls.length) {
            formSelectEls.forEach(formSelectEl => {
                new Choices(formSelectEl, {
                    searchEnabled: false
                });
            })
        }

        new Swiper('.our-areas-list__content .swiper', {
            slidesPerView: 1,
            spaceBetween: 12,
            pagination: {
                el: '.our-areas-list__pagination',
                type: 'bullets',
            },
            breakpoints: {
                992: {
                    slidesPerView: 4,
                    spaceBetween: 24,
                },
                768: {
                    slidesPerView: 3,
                    spaceBetween: 18,
                },
                600: {
                    spaceBetween: 16,
                    slidesPerView: 2,
                }
            }
        });

        function isElementInViewport(el) {
            const rect = el.getBoundingClientRect();
            const offset = 0; // Можно добавить отступ, если требуется

            return (
                rect.bottom >= offset && // Нижняя часть элемента больше или равна 0 (т.е. появилась снизу)
                rect.top <= (window.innerHeight || document.documentElement.clientHeight) // Верхняя часть элемента видима на экране
            );
        }

        const animatedElements = document.querySelectorAll('[data-animation]');
        let elementsToAnimate = Array.from(animatedElements);

        function onScroll() {
            elementsToAnimate = elementsToAnimate.filter(animatedElement => {
                if (isElementInViewport(animatedElement)) {
                    animatedElement.setAttribute('data-activated', true);
                    return false;
                }
                return true;
            });

            if (elementsToAnimate.length === 0) {
                window.removeEventListener('scroll', onScroll);
            }
        }

        if (animatedElements.length > 0) {
            window.addEventListener('scroll', onScroll);
            window.addEventListener('load', onScroll);
        }

    }
);