import debounce from "lodash/debounce";
import {isNull} from "lodash/lang";
import {slideDown, slideUp} from "./utils";

export class RunHeader {
    constructor(menu) {
        this._scrolledLogic = this._scrolledLogic.bind(this);

        this._header = menu;
        this._body = document.querySelector('body');
        this._burger = this._header.querySelector('.header-content__nav');
        this._burgerIcon = this._header.querySelector('.header-content__burger-trigger');
        this._subItems = this._header.querySelectorAll('[data-sub-trigger]');
        this._categoriesItems = new Map();
        this._addHandlers();
        this._isDesktop = window.innerWidth >= 992;

        this._lastScrollTop = 0;
        this._scrollThreshold = 150;
        this._scrolledLogic();
    }
    _scrolledLogic() {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop;

        if (scrollTop > this._scrollThreshold) {
            if (scrollTop < this._lastScrollTop) {
                this._header.classList.add('header--scrolled-up');
                this._header.classList.remove('header--scrolled-down');
            } else {
                this._header.classList.add('header--scrolled-down');
                this._header.classList.remove('header--scrolled-up');
            }
        } else {
            this._header.classList.remove('header--scrolled-up');
            this._header.classList.remove('header--scrolled-down');
        }

        this._lastScrollTop = scrollTop;
    }

    _clickOutBurger = ({target}) => {
        if (!this._header.hasAttribute('data-animating')) {
            if (!target.classList.contains('header-content') && !target.closest('.header-content')) {
                this._closeBurger();
            }
        }
    }

    _openBurger() {
        this._header.setAttribute('data-open', true);
        this._burgerIcon.setAttribute('data-open', true);
        this._body.classList.add('blocked');
        document.addEventListener('click', this._clickOutBurger);
        slideDown(this._burger, 300, () => {
            this._header.removeAttribute('data-animating');
        });
    }

    _resetStylesForDesktop() {
        this._burgerIcon.removeAttribute('data-open');
        this._header.removeAttribute('data-open');
        this._body.classList.remove('blocked');
        this._burger.style = '';
        this._isDesktop = true;
        this._closeAllSubCategories(null);
        document.removeEventListener('click', this._clickOutBurger);
    }

    _closeBurger() {
        this._burgerIcon.removeAttribute('data-open');
        this._closeAllSubCategories(null);
        document.removeEventListener('click', this._clickOutBurger);
        slideUp(this._burger, 300, () => {
            this._header.removeAttribute('data-animating');
            this._header.removeAttribute('data-open');
            this._body.classList.remove('blocked');
        });
    }


    _addHandlers() {
        this._burgerIcon.addEventListener('click', () => {
            if (!this._header.hasAttribute('data-animating')) {
                this._header.setAttribute('data-animating', true);
                if (this._header.hasAttribute('data-open')) {
                    this._closeBurger();
                } else {
                    this._openBurger();
                }
            }
        })

        window.addEventListener('resize', debounce(() => {
            this._isDesktop = window.innerWidth >= 992;

            if (this._isDesktop) {
                this._resetStylesForDesktop();
            }

        }, 100));

        window.addEventListener('scroll', this._scrolledLogic);

        this._subItems.forEach((cat, ind) => {
            const subCat = cat.querySelector(`[data-sub-menu]`);
            const subIcon = cat.querySelector(`[data-sub-icon]`);
            if (subCat) {
                this._categoriesItems.set(cat, {
                    submenuElement: subCat,
                    timeoutId: null,
                    open: ind === 0,
                    subIcon: subIcon,
                });
                cat.addEventListener('mouseenter', this._categoryMouseEnterHandler.bind(this, cat));
                cat.addEventListener('mouseleave', this._categoryMouseLeaveHandler.bind(this, cat));
                subIcon.addEventListener('click', this._categoryClickHandler.bind(this, cat));
            }
        });
    }

    _categoryClickHandler(category) {
        if (!this._isDesktop) {
            let submenu = this._categoriesItems.get(category);
            const submenuElement = submenu.submenuElement;
            if (!category.hasAttribute('data-animating')) {
                category.setAttribute('data-animating', true);
                if (category.hasAttribute('data-active')) {
                    this._closeSubcat(category);
                } else {
                    this._openSubCat(category);
                }
                category.removeAttribute('data-animating');
            }
        }
    }

    _closeSubcat(category) {
        let submenu = this._categoriesItems.get(category);
        const submenuElement = submenu.submenuElement;
        if (submenuElement) {
            submenuElement.removeAttribute('data-active');
            category.removeAttribute('data-active');
            submenu.open = false;
        }
    }

    _openSubCat(category) {
        let submenu = this._categoriesItems.get(category);
        const submenuElement = submenu.submenuElement;
        if (submenuElement) {
            submenuElement.setAttribute('data-active', true);
            category.setAttribute('data-active', true);
            submenu.open = true;
        }
    }

    _closeAllSubCategories(currentCategory) {
        for (let [category, submenu] of this._categoriesItems) {
            if ((submenu.open === true && isNull(currentCategory)) || (submenu.open === true && currentCategory && !category.contains(currentCategory.submenuElement))) {
                this._closeSubcat(category);
            }
        }
    }

    _categoryMouseLeaveHandler(category) {
        if (this._isDesktop) {
            const submenu = this._categoriesItems.get(category);
            submenu.timeoutId = setTimeout(() => {
                this._closeSubcat(category);
            }, 250);
        }
    }

    _categoryMouseEnterHandler(category) {
        if (this._isDesktop) {
            const submenu = this._categoriesItems.get(category);
            if (submenu.timeoutId) {
                clearTimeout(submenu.timeoutId);
                submenu.timeoutId = null;
            }
            if (this._categoriesItems.has(category)) {
                let submenu = this._categoriesItems.get(category);
                this._closeAllSubCategories(submenu);
                this._openSubCat(category);
            }
        }
    }

}
